@import '../../node_modules/swiper/swiper-bundle.css';

@font-face {
  font-family: "Fira-Sans";
  src: url("../fonts/../fonts/FiraSans-ExtraBold.woff2") format("woff2");
  font-weight: 800;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Fira-Sans";
  src: url("../fonts/../fonts/FiraSans-Bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Fira-Sans";
  src: url("../fonts/../fonts/FiraSans-Medium.woff2") format("woff2");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Fira-Sans";
  src: url("../fonts/../fonts/FiraSans-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Fira-Sans";
  src: url("../fonts/../fonts/FiraSans-Light.woff2") format("woff2");
  font-weight: 300;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Fira-Sans";
  src: url("../fonts/../fonts/FiraSans-Thin.woff2") format("woff2");
  font-weight: 100;
  font-display: swap;
  font-style: normal;
}

/* Reset styles */

* {
  padding: 0;
  margin: 0;
  border: 0;
  outline: 0;
  vertical-align: baseline;
  background: transparent;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a {
  color: inherit;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

nav ul {
  list-style: none;
}

img {
  vertical-align: top;
}

nav,
footer,
header,
aside,
label {
  display: block;
}

input,
button,
textarea {
  font-family: inherit;
  color: inherit;
}

::placeholder {
  color: inherit;
  font-family: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 400;
}

sub {
  vertical-align: sub;
  font-size: smaller;
}

sup {
  vertical-align: super;
  font-size: smaller;
}

:root {
  scroll-behavior: smooth;
}

html {
  scrollbar-gutter: stable;
}

.hide {
  opacity: 0;
}

.mask {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: #383B45;
  z-index: 100;
  display: grid;
  place-content: center;
}

.loader {
  display: grid;
  place-content: center;
  width: 125px;
  height: 125px;
  border: solid 10px rgba(0, 0, 0, 0.1);
  border-radius: 50%;
  border-left-color: #4AF6CD;
  animation: load 1.3s linear infinite;
}

@keyframes load {
  100% {
    transform: rotate(360deg);
  }
}

.nav {
  width: 100%;
  height: 353px;
  margin-bottom: 17px;
}

.nav__list {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
  gap: 0px;
}

.nav__item {
  width: 100%;
}

.nav__link {
  width: 100%;
  transition: all 0.3s;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
  gap: 6px;
}

.nav__link:hover {
  background-color: #4AF6CD;
  transition: all 0.3s;
}

.nav__svg {
  transition: all 0.3s;
}

.nav__svg:nth-child(1) {
  width: 61px;
  height: 48px;
}

.nav__svg:nth-child(2) {
  width: 52px;
  height: 33px;
}

.nav__svg:nth-child(3) {
  width: 47px;
  height: 49px;
}

.nav__svg:nth-child4(4) {
  width: 25px;
  height: 52px;
}

.nav__text {
  font-weight: 500;
  font-size: 12px;
  line-height: 1.16667;
  letter-spacing: 0.25em;
  text-align: center;
  text-transform: capitalize;
}

.burger {
  display: none;
  position: absolute;
  left: 25px;
  bottom: auto;
  top: 25px;
  width: 28px;
  height: 20px;
  transition: all 0.3s;
  z-index: 10;
  cursor: pointer;
  transition: all 0.5s;
}

.burger-active {
  transform: translateY(calc(100dvh - 80px));
  transition: all 0.5s;
}

.burger:active .burger__span {
  background-color: #4AF6CD;
}

.burger:focus .burger__span {
  background-color: #4AF6CD;
}

.burger__span {
  position: absolute;
  left: 0;
  width: 100%;
  height: 4px;
  background-color: #fff;
  transition: all 0.3s;
}

.burger__span-one {
  top: 0;
}

.burger__span-two {
  top: 50%;
}

.burger__span-three {
  top: 100%;
}

.weather-forcast {
  position: absolute;
  top: 50%;
  left: 50%;
  translate: -50% -50% 0;
  width: 100%;
  max-width: 283px;
  padding: 28px 32px;
  border-radius: 6px;
  background-color: rgba(15, 16, 16, 0.7);
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
  gap: 20px;
}

.weather-forcast__names {
  width: 100%;
  height: 71px;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-direction: row;
  gap: 0px;
}

.weather-forcast__shore {
  position: relative;
  margin-bottom: 16px;
  font-weight: 800;
  font-size: 18px;
}

.weather-forcast__shore::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: -5px;
  width: 80px;
  height: 1px;
  background-color: #4AF6CD;
}

.weather-forcast__country {
  font-weight: 400;
  font-size: 16px;
  line-height: 1.1875;
  text-transform: capitalize;
}

.weather-forcast__svg {
  fill: #fff;
  transition: all 0.3s;
}

.weather-forcast__link:hover .weather-forcast__svg {
  fill: #4AF6CD;
}

.weather-forcast__data {
  width: 100%;
  padding: 0 8px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  gap: 0px;
}

.weather-forcast__item {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 5px;
}

.weather-forcast__item-svg {
  fill: #4AF6CD;
}

.weather-forcast__item-numbers {
  font-weight: 800;
  font-size: 16px;
  line-height: 1.4375;
  text-transform: lowercase;
}

.weather-forcast__item-names {
  font-size: 10px;
  font-weight: 300;
  line-height: 1.2;
}

/* global styles*/

body {
  font-family: "Fira-Sans", sans-serif;
  font-style: normal;
  font-weight: 800;
  font-size: 16px;
  line-height: 1.6;
  text-transform: uppercase;
  color: #fff;
  background-color: rgb(23, 25, 25);
  overflow-x: hidden;
}

.title {
  margin-left: -4.034%;
}

.title h2 {
  display: inline-block;
  position: relative;
  font-weight: 800;
  font-size: clamp(100px, 3.3088rem + 14.7059vw, 200px);
  line-height: 0.78;
  color: rgba(255, 255, 255, 0.02);
}

.title h2 span {
  display: inline-block;
  position: absolute;
  top: -20px;
  right: -14px;
  font-weight: 800;
  font-size: 80px;
  color: #fff;
}

.title h2 span::after {
  content: "";
  position: absolute;
  top: -15px;
  right: 0;
  width: 80px;
  height: 1px;
  background-color: #4AF6CD;
}

.headline-left {
  display: inline-block;
  position: relative;
  font-weight: 400;
  font-size: clamp(16px, 0.8235rem + 0.8824vw, 22px);
  line-height: 1.7;
  letter-spacing: 7.33333px;
  text-transform: uppercase;
  color: rgba(255, 255, 255, 0.5);
}

.headline-left::before {
  content: "";
  position: absolute;
  left: -103px;
  top: 50%;
  width: clamp(50px, 2.2426rem + 4.4118vw, 80px);
  height: 1px;
  background-color: rgba(255, 255, 255, 0.5);
}

.slider-title {
  font-weight: 800;
  font-size: clamp(24px, 1.1735rem + 1.6327vw, 40px);
  line-height: 1.5;
  text-transform: uppercase;
  color: #fff;
}

.no-scroll {
  overflow: hidden;
}

/* Header styles */

.header {
  position: relative;
  width: 100%;
  min-height: 1024px;
}

.header__slider {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  overflow: hidden;
}

.header__slide {
  position: relative;
  width: 100%;
  min-height: 1024px;
  background-color: rgb(23, 25, 25);
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  overflow: hidden;
}

.header__slide::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(62.09% 62.09% at 42.87% 49.23%, rgba(34, 34, 34, 0.43) 0%, #0f0f0f 100%);
  z-index: 0;
}

.header__slide-map {
  position: absolute;
  width: clamp(400px, 5.4103rem + 97.9487vw, 1164px);
  height: clamp(432px, 5.5897rem + 107.0513vw, 1267px);
  top: 280px;
  right: 55px;
  transform: translate(15.5%, -83%);
  z-index: 1;
}

.header__slide-content {
  position: absolute;
  right: 58px;
  bottom: 87px;
  text-align: right;
  max-width: 520px;
  z-index: 1;
}

.header__slide-subtitle {
  margin-bottom: 10px;
}

.header__slide-title {
  font-weight: 800;
  font-size: clamp(35px, 1.2691rem + 4.5918vw, 80px);
  line-height: 1.1;
  text-transform: uppercase;
  margin-bottom: 27px;
}

.header__slide-subtext {
  margin-bottom: 35px;
}

.header__slide-text {
  margin-bottom: 20px;
}

.header__slide-link {
  position: relative;
  display: inline-block;
  width: 25px;
  height: 21px;
  z-index: 2;
}

.header__slide-link:hover .header__slide-content-arrow {
  fill: #4AF6CD;
  transition: all 0.3s;
}

.header__slide-content-arrow {
  width: 24px;
  height: 20px;
  fill: #fff;
}

.header__slider-control {
  position: absolute;
  top: 38px;
  right: 62px;
  z-index: 100;
  width: 78px;
  height: 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  gap: 0px;
}

.header__slider-prev {
  width: 29px;
  height: 30px;
  position: relative;
  z-index: 100;
}

.header__slider-prev:hover .slider-arrow {
  fill: #4AF6CD;
  transition: all 0.3s;
}

.header__slider-next {
  width: 29px;
  height: 30px;
  position: relative;
  z-index: 100;
}

.header__slider-next:hover .slider-arrow {
  fill: #4AF6CD;
  transition: all 0.3s;
}

.header .swiper-pagination {
  position: absolute;
  left: auto;
  right: 67px;
  bottom: 22px;
  width: 100%;
  max-width: 920px;
  height: 44px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  flex-direction: row;
  gap: 40px;
}

.header .swiper-pagination-bullets .swiper-pagination-bullet {
  position: relative;
  margin: 0;
  width: 100%;
  max-width: 200px;
  height: 44px;
  border-top: 5px solid #fff;
  opacity: 1;
  border-radius: 0;
  background-color: transparent;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  gap: 0px;
}

.header .swiper-pagination-bullets .swiper-pagination-bullet::before {
  content: "";
  position: absolute;
  left: 0;
  top: -5px;
  width: 0;
  height: 5px;
  background-color: #4AF6CD;
  transition: all 0.3s;
}

.header .swiper-pagination-bullets .swiper-pagination-bullet-active {
  border-top: 5px solid transparent;
  transition: all 1s;
}

.header .swiper-pagination-bullets .swiper-pagination-bullet-active::before {
  content: "";
  position: absolute;
  left: 0;
  top: -5px;
  width: 100%;
  height: 5px;
  background-color: #4AF6CD;
  transition: all 1s;
}

.header__pagination-number {
  font-size: clamp(16px, 0.7647rem + 1.1765vw, 24px);
  line-height: 1.41667;
}

.header__pagination-shore {
  font-weight: 300;
  font-size: clamp(12px, 0.6324rem + 0.5882vw, 16px);
  line-height: 1.1875;
  text-transform: capitalize;
}

.header__title {
  position: absolute;
  top: 0;
  left: -20px;
  z-index: 2;
}

.header__title span {
  display: block;
  font-weight: 800;
  font-size: clamp(100px, 3.3088rem + 14.7059vw, 200px);
  line-height: 0.78;
  color: rgba(255, 255, 255, 0.07);
}

.header__aside {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  max-width: 164px;
  border-right: 1px solid rgba(255, 255, 255, 0.25);
  z-index: 2;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
  gap: 0px;
}

.header__aside-inner {
  width: 100%;
}

.header__logo {
  display: block;
  width: 100%;
  height: 258px;
  text-align: center;
  padding-top: 22px;
  margin-bottom: 90px;
}

.header__logo::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 181px;
  background-color: #4AF6CD;
  z-index: 0;
}

.header__logo-svg {
  position: relative;
  width: 86px;
  height: 258px;
  fill: #fff;
}

.header__search {
  width: 100%;
  height: 73px;
  margin-bottom: 7px;
  background-color: transparent;
}

.header__search:hover {
  background-color: #4AF6CD;
  transition: all 0.3s;
}

.header__search-svg {
  width: 31px;
  height: 31px;
  fill: #fff;
}

.header__date-outer {
  margin-bottom: 40px;
}

.header__date-outer-clone {
  display: none;
}

.header__date {
  margin-bottom: 25px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
  gap: 0px;
}

.header__date-day {
  font-size: 72px;
  line-height: 0.88889;
}

.header__date-month {
  font-weight: 300;
  font-size: 16px;
  line-height: 1.1875;
  letter-spacing: 0.09em;
  text-transform: lowercase;
}

.header__date-devide {
  font-weight: 300;
  font-size: 16px;
  line-height: 1.1875;
  text-transform: lowercase;
}

.header__date-year {
  font-weight: 300;
  font-size: 16px;
  line-height: 1.1875;
  letter-spacing: 0.09em;
  text-transform: lowercase;
}

.header__place {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  gap: 9px;
}

.header__place-name {
  font-weight: 300;
  font-size: 12px;
  line-height: 1.6;
  letter-spacing: 1.05px;
  text-transform: capitalize;
}

.header__marker-svg {
  width: 11px;
  height: 13px;
  fill: #fff;
}

.header__scroll-arrow {
  display: inline-block;
  position: absolute;
  bottom: 41px;
  left: 205px;
  padding: 10px;
  z-index: 2;
  animation-name: moving;
  animation-fill-mode: forwards;
  animation-duration: 2s;
  animation-timing-function: cubic-bezier(0.48, 0.62, 0.58, 1);
  animation-iteration-count: infinite;
}

.header__scroll-svg {
  width: 22px;
  height: 24px;
}

.header .slider-arrow {
  width: 24px;
  height: 20px;
  fill: #fff;
}

.header .map-north {
  opacity: 0;
  transition: all 2s;
}

.header .map-visible {
  opacity: 1;
  transition: all 1s;
  transition-delay: 1s;
}

.header .road {
  animation-name: road-start;
  animation-fill-mode: forwards;
  animation-duration: 4s;
  animation-delay: 1s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  stroke: #fff;
  transition: all 1s linear;
  stroke-dashoffset: 600;
  stroke-dasharray: 600;
}

@keyframes road-start {
  to {
    stroke-dashoffset: 0;
  }
}

@keyframes moving {
  0% {
    transform: translateY(0);
  }

  25% {
    transform: translateY(15px);
  }

  50% {
    transform: translateY(0);
  }

  75% {
    transform: translateY(8px);
  }

  100% {
    transform: translateY(0);
  }
}

.surf {
  width: 100%;
  padding-top: clamp(40px, -0.2321rem + 13.6607vw, 193px);
  padding-bottom: clamp(40px, 0.7143rem + 8.9286vw, 140px);
}

.surf__titles-box {
  width: 100%;
  margin-bottom: 20px;
  padding-right: 78px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  gap: 0px;
}

.surf__subtitles-box {
  translate: 0 85px 0;
}

.surf__slide-subtitle {
  translate: 10px 0 0;
}

.surf__slider-title {
  text-align: right;
}

.surf__slider-devider {
  font-weight: 300;
  color: rgba(255, 255, 255, 0.5);
}

.surf__map {
  position: relative;
  width: 100%;
  aspect-ratio: 1.94/1;
  margin: 0 auto;
  padding: 0 102px 56px 45px;
}

.surf__map-svg {
  width: 100%;
  max-width: 1233px;
  aspect-ratio: 1.94/1;
}

.surf__map-coordinates {
  position: absolute;
  right: 5.2%;
  top: 172px;
  font-weight: 400;
  font-size: 18px;
  line-height: 1.22222;
  letter-spacing: 8.7px;
  text-align: right;
  color: rgba(255, 255, 255, 0.5);
  writing-mode: vertical-lr;
  rotate: 180deg;
}

/* modals styles*/

.modal-window {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  padding: 15px;
  z-index: -50;
  opacity: 0;
  pointer-events: none;
  transition: 0.3s;
  background: rgba(0, 0, 0, 0.5);
  overflow-y: auto;
  overflow-x: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}

.modal-window--visible {
  z-index: 50;
  opacity: 1;
  pointer-events: all;
}

@media (min-width: 1440px) {
  .title {
    margin-left: -50px;
  }
}

@media (max-width: 1300px) {
  .header .swiper-pagination {
    max-width: 650px;
    gap: 20px;
  }
}

@media (max-width: 1024px) {
  .header {
    height: 100dvh;
    min-height: 700px;
  }

  .header__slide {
    height: 100dvh;
    min-height: 700px;
  }

  .header__slide-map {
    transform: translate(16%, -84%);
  }

  .header__slide-title {
    margin-bottom: 17px;
  }

  .header__slide-subtitle {
    margin-bottom: 5px;
  }

  .header__slide-text {
    margin-bottom: 10px;
  }

  .header .swiper-pagination {
    max-width: 550px;
    gap: 20px;
  }

  .header__aside {
    max-width: 110px;
  }

  .header__logo {
    height: 212px;
    padding-top: 0;
    margin-bottom: 48px;
  }

  .header__logo::before {
    content: "";
    height: 124px;
  }

  .header__logo-svg {
    width: 60px;
    height: 202px;
  }

  .header__scroll-arrow {
    left: 140px;
  }
}

@media (max-width: 1000px) {
  .headline-left::before {
    content: "";
    left: -83px;
  }
}

@media (max-width: 850px) {
  .header__scroll-arrow {
    display: none;
  }

  .header .swiper-pagination {
    max-width: 410px;
    gap: 15px;
  }

  .header .swiper-pagination-bullets .swiper-pagination-bullet {
    justify-content: flex-end;
  }

  .header__pagination-number {
    display: none;
  }

  .header__slide-map {
    transform: translate(17%, -86%);
  }
}

@media (max-width: 768px) {
  .headline-left::before {
    content: "";
    left: -73px;
  }

  .header__slide-map {
    transform: translate(18%, -87%);
  }
}

@media (max-width: 650px) {
  .nav__svg:nth-child(1) {
    width: 51px;
